<template>
  <div class="category">
    <div class="headerTop">
      <!--  头部  -->
      <buyHeader>
        <router-link to="/" slot="home" class="home-icon header-icon">
          <span class="icon iconfont buy-icon-shouye"></span>
        </router-link>
        <!-- logo-->
        <img
          height="20"
          src="../assets/img/logo.png"
          alt="logo"
          class="buyHeadLogo header-icon"
          slot="logo"
        />
        <!--购物车-->
        <div
          class="cartIcon header-icon"
          alt="cart"
          slot="cart"
          style="float: right"
        >
          <span class="icon iconfont buy-icon-gouwuche"></span>
        </div>
        <!--搜索-->
        <div
          class="buySearch header-icon"
          alt="search"
          slot="search"
          style="float: right"
        >
          <span class="buyGlassIcon icon iconfont  buy-icon-sousuo"></span>
        </div>
      </buyHeader>
      <!--  头部下方分类  -->
      <div class="buyClassCon">
        <classList
          :classList="classList"
          :current="current"
          @changeHandler="changeHandler"
        ></classList>
      </div>
    </div>
    <div class="goodsCont" style="padding-top: 85px;" v-if="componenFlag">
      <div class="classGoodsCont">
        <div
          class="homeList"
          v-for="(item1, index1) of classifyInfo"
          :key="index1"
        >
          <div class="imgCont" :style="conheight" ref="classGoodsItem">
            <img
              v-lazy="item1.head_imgs ? config.cdnbase + item1.head_imgs : ''"
              alt=""
              :style="conheight"
            />
          </div>
          <div class="classText">
            <div class="promotion">
              <span>{{ item1.promotion_name1 }}</span>
              <span>{{ item1.promotion_name2 }}</span>
            </div>
            <div class="className">
              <span class="homeSitename">{{ item1.sitename }}</span>
            </div>
            <div class="homePrice">{{ item1.money }}{{ item1.price }}</div>
            <div>
              <p class="status gradientPrice">特价</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { classify } from "../lib/goods";
import classList from "../components/home/classList";
import { mapState } from "vuex";

export default {
  name: "category",
  data() {
    return {
      classifyInfo: [],
      classList: [],
      current: "",
      conheight: {
        height: ""
      },
      componenFlag: true
    };
  },
  created() {
    let id = this.$route.params.id;
    let mid = this.$route.params.mid;
    this.getClassifyInfo({ id, mid });
    this.getSecondClass(id);
  },
  mounted() {},
  methods: {
    async getClassifyInfo(obj) {
      this.classifyInfo = await classify.get_home_productAll(obj);
      console.log(this.classifyInfo);
      if (!obj.mid) {
        this.current = "全部";
      }
    },
    getSecondClass(id) {
      this.classInfo.map(item => {
        if (item.id + "" === id + "") {
          item.subclass.map(item1 => {
            this.classList.push(item1.name);
          });
          this.classList.unshift("全部");
        }
      });
    },
    changeHandler(cur) {
      let id = this.$route.params.id;
      this.classInfo.map(item => {
        if (item.id + "" === id + "") {
          item.subclass.map(item1 => {
            if (item1.name === cur) {
              this.getClassifyInfo({ id, mid: item1.id });
              this.current = cur;
              this.componenFlag = false;
            }
          });
        }
      });
    },
    getHeight() {
      this.conheight.height = this.$refs.classGoodsItem[0].offsetWidth + "px";
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"])
  },
  watch: {
    classifyInfo: function() {
      this.$nextTick(() => {
        this.componenFlag = true;
        this.getHeight();
      });
    }
  },
  components: {
    classList
  }
};
</script>

<style scoped lang="stylus">
.headerTop
  position fixed
  top 0
  z-index 50
  max-width 768px
  left 0
  right 0
  margin auto
  .buyHeader
    background #fafafa
    padding 10px 10px
    text-align left
    border-bottom 1px solid #d9d9d9
    position relative
  .header-icon
    display inline-block
    vertical-align middle
    .buy-icon-shouye
      font-size 23px
  .buyHeadLogo
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    height 23px
  .buySearch
    margin-left auto
    .buyGlassIcon
      font-size 23px
      margin-right 15px
  .cartIcon
    margin-right 5px
    span
      font-size 23px

.buyClassCon
  >>> .cube-scroll-nav-bar-item
    padding 10px 10px !important

.classGoodsCont
  text-align left
  .homeList
    width 50%
    display inline-block
    vertical-align top
    box-sizing border-box
    .imgCont
      background #f4f4f4
      img
        width 100%
    .classText
      .promotion
        background: #F1ECE2;
        border-radius: 0 0 3px 3px;
        font-size: 14px;
        color: #9F8A60;
        line-height: 18px;
        height 18px
        padding: 2px 5px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      .className
        font-size 14px
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        padding 8px 0
      .homePrice
        font-size: 16px;
        line-height: 20px;
        color: #b4282d;
      .status
        margin: 3px 0;
        padding: 2px;
        line-height: 10px;
        height: 10px;
        text-align: center;
        color: #CE2424;
        background: rgba(255, 255, 255, .9);
        border: 1px solid #CE2424;
        font-size: 12px;
        border-radius: 7.5px;
        display: inline-block;
        vertical-align: middle;
  .homeList:nth-child(2n)
    padding 0 10px 10px 5px
  .homeList:nth-child(2n+1)
    padding 0 5px 10px 10px
</style>
